import { BackTop } from 'antd'
import React, { useState, useEffect } from 'react'
import { Paginationn } from '../Pagination/Pagination'
import { PokePage } from '../PokePage/PokePage'
import { Searchh } from '../Search/Search'

export const Content = () => {

    const [pokeCount, setpokeCount] = useState()
    const [page_Size, setpage_Size] = useState(20)
    const [page_Number, setpage_Number] = useState(0)
    const [search, setSearch] = useState('')
    const [Url, setUrl] = useState(`https://pokeapi.co/api/v2/pokemon?limit=${page_Size-1}&offset=${page_Number}`)

    useEffect(() => {
        search===''?
        setUrl(`https://pokeapi.co/api/v2/pokemon?limit=${page_Size-1}&offset=${page_Number}`):
        setUrl(`https://pokeapi.co/api/v2/pokemon?limit=1050&offset=0`)
    });

function onShowSizeChange(current, pageSize) {
    setpage_Size(pageSize)
    console.log(current, pageSize)
    console.log(page_Size)
}
function onChange(pageNumber) {
    console.log('Page: ', pageNumber)
    setpage_Number(pageNumber === 1 ? 1 :pageNumber*page_Size)
    console.log('ofset: ', pageNumber)
}

    return (
        <div>
            <Searchh
            setSearch={setSearch}
            />
            {
                console.log(search)
            }
            <Paginationn
            pokeCount={pokeCount}
            onChange={onChange}
            onShowSizeChange={onShowSizeChange}
            />
            <PokePage
            setpokeCount={setpokeCount}
            page_Size={page_Size}
            page_Number={page_Number}
            Url={Url}
            search={search}
            />
            <Paginationn
            pokeCount={pokeCount}
            onChange={onChange}
            onShowSizeChange={onShowSizeChange}
            />
            <BackTop>
                <p>Up</p>
            </BackTop>
        </div>
    )
}
